$htmlSize: 14;

@function toRem($value) {
	$remValue: calc($value / $htmlSize); 
	@return $remValue * 1rem;
}

@mixin hideInput() {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0;
  height: 0;
}

@mixin iconWithGradient($gradient-color) {
  background: $gradient-color;
  background-clip: text;
  color: transparent;
}

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-10: rgba(31, 32, 65, 0.10);
$dark-shade-5: rgba(31, 32, 65, 0.05);

$grey: #F4F4F6;

$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$purple-25: #BC9CFF25;

$green: #6FCF97;

$jordy-blue: #8BA4F9;
$jordy-blue-50: #8BA4F950;
$jordy-blue-25: #8BA4F925;

$sky-blue: #66D2EA;

$primary-gradient: linear-gradient(180deg, $purple 0%, $jordy-blue 100%);
$primary-gradient-50: linear-gradient(180deg, $purple-50 0%, $jordy-blue-50 100%);
$primary-gradient-25: linear-gradient(180deg, $purple-25 0%, $jordy-blue-25 100%);

$secondary-gradient: linear-gradient(180deg, $green 0%, $sky-blue 100%);

$small: toRem(150);
$medium: toRem(266);
$big: toRem(320);

$xs: toRem(768);
$sm: toRem(800);
$md: toRem(900);
$lg: toRem(1000);
$xl: toRem(1120);
$xxl: toRem(1200);

$checkbox-size: toRem(20);
$checkbox-margin: toRem(10);

$range-slider-height: toRem(6);
$className: 'room-price-item';

.#{$className} {
  display: flex;
  align-items: flex-start;

  &__content {
    max-width: toRem(220);
    width: 100%;
    line-height: toRem(18);
  }

  &__details {
    position: relative;
    right: 0;
    line-height: toRem(0);
  }
  &__details-icon {
    cursor: pointer;
    width: toRem(20);
    height: toRem(20);
    color: $dark-shade-25;
    font-size: toRem(12);
    font-weight: 700;
    border: toRem(1) solid $dark-shade-25;
    border-radius: 50%;

    &:hover + .#{$className}__details-content,
    &:focus + .#{$className}__details-content {
      opacity: 1;
      visibility: visible;
      transform: translateY(toRem(15));
    }
  }

  &__details-content {
    position: absolute;
    font-size: toRem(12);
    line-height: toRem(14);
    border: toRem(1) solid $dark-shade-50;
    box-shadow: toRem(0) toRem(0) toRem(10) rgba(0, 0, 0, 0.2);
    padding: toRem(5) toRem(5) toRem(5);
    border-radius: toRem(3);
    transition: opacity 0.4s, visibility 0.4s, transform 0.4s;  
    transform: translateY(toRem(20));
    opacity: 0;
    visibility: hidden;
    min-width: toRem(100);
    background-color: white;
    z-index: 1;

    &::before {
      content: '';
      position: absolute;
      width: toRem(10);
      height: toRem(10);
      background-color: white;
      border-left: toRem(1) solid $dark-shade-50;
      border-top: toRem(1) solid $dark-shade-50;
      transform: translateY(toRem(-6.25)) rotate(45deg);
      top: 0;
    }
  }

  &__price {
    margin-left: auto;
    line-height: toRem(18);
  }
  
}