$htmlSize: 14;

@function toRem($value) {
	$remValue: calc($value / $htmlSize); 
	@return $remValue * 1rem;
}

@mixin hideInput() {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0;
  height: 0;
}

@mixin iconWithGradient($gradient-color) {
  background: $gradient-color;
  background-clip: text;
  color: transparent;
}

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-10: rgba(31, 32, 65, 0.10);
$dark-shade-5: rgba(31, 32, 65, 0.05);

$grey: #F4F4F6;

$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$purple-25: #BC9CFF25;

$green: #6FCF97;

$jordy-blue: #8BA4F9;
$jordy-blue-50: #8BA4F950;
$jordy-blue-25: #8BA4F925;

$sky-blue: #66D2EA;

$primary-gradient: linear-gradient(180deg, $purple 0%, $jordy-blue 100%);
$primary-gradient-50: linear-gradient(180deg, $purple-50 0%, $jordy-blue-50 100%);
$primary-gradient-25: linear-gradient(180deg, $purple-25 0%, $jordy-blue-25 100%);

$secondary-gradient: linear-gradient(180deg, $green 0%, $sky-blue 100%);

$small: toRem(150);
$medium: toRem(266);
$big: toRem(320);

$xs: toRem(768);
$sm: toRem(800);
$md: toRem(900);
$lg: toRem(1000);
$xl: toRem(1120);
$xxl: toRem(1200);

$checkbox-size: toRem(20);
$checkbox-margin: toRem(10);

$range-slider-height: toRem(6);
$className: 'card-room';

.#{$className} {
  color: $dark-shade-50;
  background-color: white;
  width: toRem(270);
  max-height: toRem(257);
  box-shadow: toRem(0) toRem(10) toRem(20) $dark-shade-5;
  border-radius: toRem(4);

  &__slider {
    display: flex;
    max-height: toRem(151);
    overflow: hidden;
    border-radius: toRem(4) toRem(4) 0 0;
    position: relative;
  }

  &__slider-image {
    width: 100%;
    display: none;

    &_active {
      display: initial;
    }
  }

  &__slider-navigation {
    position: absolute;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-between;
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s 300ms, opacity 300ms;
  }

  &__previous,
  &__next {
    height: 100%;
    width: toRem(42);
    display: flex;
    align-items: center;
    cursor: pointer;
  }
  
  &__previous {
    background: linear-gradient(90deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 99.99%, rgba(255, 255, 255, 0) 100%);
  }

  &__next {
    background: linear-gradient(-90deg, rgba(0, 0, 0, 0.5) 0%, rgba(0, 0, 0, 0) 99.99%, rgba(255, 255, 255, 0) 100%);
  }

  &__previous-icon,
  &__next-icon {
    color: white;
    margin: 0 auto;
    width: toRem(24);
    height: toRem(24);
  }

  &__previous-icon {
    transform: rotate(90deg);
  }

  &__next-icon {
    transform: rotate(-90deg);
  }

  &__control-panel {
    position: absolute;
    display: flex;
    right: toRem(15);
    bottom: toRem(10);
    list-style-type: none;
    z-index: 1;
  }

  &__control-panel-item {
    margin-right: toRem(3.75);

    &:last-child {
      margin-right: initial;
    }
  }

  &__control-panel-button {
    cursor: pointer;
    width: toRem(8);
    height: toRem(8);
    border-radius: 50%;
    border: toRem(1) solid #FFFFFF;

    &_active {
      background: white;
    }
  }

  &__info {
    margin: toRem(21) toRem(20) toRem(20);
    padding-bottom: toRem(12);
  }

  &__rating-and-reviews {
    display: flex;
    justify-content: space-between;
  }

  &__label {
    border-bottom: toRem(1) solid $dark-shade-10;
    padding-bottom: toRem(9);
  }

  &__rating-and-reviews {
    margin-top: toRem(8);
  }

  &__reviews-count,
  &__reviews-title {
    line-height: toRem(18);
  }

  &__reviews-count {
    font-weight: 700;
  }

  &:hover,
  &_hovered {
    .#{$className}__slider-navigation {
      visibility: visible;
      opacity: 1;
      transition: visibility 0s, opacity 300ms;
    }
  }

  &_hovered {
    .#{$className}__slider-navigation {
      transition: opacity 0s;
    }
  }
}