$htmlSize: 14;

@function toRem($value) {
	$remValue: calc($value / $htmlSize); 
	@return $remValue * 1rem;
}

@mixin hideInput() {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0;
  height: 0;
}

@mixin iconWithGradient($gradient-color) {
  background: $gradient-color;
  background-clip: text;
  color: transparent;
}

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-10: rgba(31, 32, 65, 0.10);
$dark-shade-5: rgba(31, 32, 65, 0.05);

$grey: #F4F4F6;

$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$purple-25: #BC9CFF25;

$green: #6FCF97;

$jordy-blue: #8BA4F9;
$jordy-blue-50: #8BA4F950;
$jordy-blue-25: #8BA4F925;

$sky-blue: #66D2EA;

$primary-gradient: linear-gradient(180deg, $purple 0%, $jordy-blue 100%);
$primary-gradient-50: linear-gradient(180deg, $purple-50 0%, $jordy-blue-50 100%);
$primary-gradient-25: linear-gradient(180deg, $purple-25 0%, $jordy-blue-25 100%);

$secondary-gradient: linear-gradient(180deg, $green 0%, $sky-blue 100%);

$small: toRem(150);
$medium: toRem(266);
$big: toRem(320);

$xs: toRem(768);
$sm: toRem(800);
$md: toRem(900);
$lg: toRem(1000);
$xl: toRem(1120);
$xxl: toRem(1200);

$checkbox-size: toRem(20);
$checkbox-margin: toRem(10);

$range-slider-height: toRem(6);
$className: 'card-reservation';

.#{$className} {
  margin: toRem(29) toRem(29) toRem(30);

  &__label {
    padding-top: toRem(10);
  }

  &__dropdown-datepicker {
    margin-top: toRem(21);
  }

  &__dropdown-guests {
    margin-top: toRem(23);
  }

  &__room-price {
    list-style-type: none;
    margin-top: toRem(20);
  }

  &__room-price-item {
    margin-top: toRem(15);

    &:first-child {
      margin: initial;
    }
  }

  &__total-price {
    margin-top: toRem(30);
    display: flex;
  }

  &__total,
  &__total-price-value {
    color: $dark-shade-100;
    font-weight: 700;
    font-size: toRem(19);
    line-height: toRem(23);
  }

  &__dots {
    width: 100%;
    max-height: toRem(19);
    border-bottom: toRem(1) dotted $dark-shade-25;
  }

  &__submit-reservation-button {
    margin-top: toRem(21);
  }
}