$htmlSize: 14;

@function toRem($value) {
	$remValue: calc($value / $htmlSize); 
	@return $remValue * 1rem;
}

@mixin hideInput() {
  position: absolute;
  z-index: -1;
  opacity: 0;
  width: 0;
  height: 0;
}

@mixin iconWithGradient($gradient-color) {
  background: $gradient-color;
  background-clip: text;
  color: transparent;
}

$dark-shade-100: rgba(31, 32, 65, 1.0);
$dark-shade-75: rgba(31, 32, 65, 0.75);
$dark-shade-50: rgba(31, 32, 65, 0.5);
$dark-shade-25: rgba(31, 32, 65, 0.25);
$dark-shade-10: rgba(31, 32, 65, 0.10);
$dark-shade-5: rgba(31, 32, 65, 0.05);

$grey: #F4F4F6;

$purple: #BC9CFF;
$purple-50: #BC9CFF50;
$purple-25: #BC9CFF25;

$green: #6FCF97;

$jordy-blue: #8BA4F9;
$jordy-blue-50: #8BA4F950;
$jordy-blue-25: #8BA4F925;

$sky-blue: #66D2EA;

$primary-gradient: linear-gradient(180deg, $purple 0%, $jordy-blue 100%);
$primary-gradient-50: linear-gradient(180deg, $purple-50 0%, $jordy-blue-50 100%);
$primary-gradient-25: linear-gradient(180deg, $purple-25 0%, $jordy-blue-25 100%);

$secondary-gradient: linear-gradient(180deg, $green 0%, $sky-blue 100%);

$small: toRem(150);
$medium: toRem(266);
$big: toRem(320);

$xs: toRem(768);
$sm: toRem(800);
$md: toRem(900);
$lg: toRem(1000);
$xl: toRem(1120);
$xxl: toRem(1200);

$checkbox-size: toRem(20);
$checkbox-margin: toRem(10);

$range-slider-height: toRem(6);
$className: 'toggle-switch';

.#{$className} {
  max-width: $medium;

  &__body {
    display: flex;
    align-items: center;
    cursor: pointer;
  }

  &__switch-input {
    @include hideInput;
  }

  &__switch-input:checked
  + &__switch {
    border-color: $purple;

    &::after {
      background-image: $primary-gradient;
      transform: translateX(toRem(19));
    }
  }

  &__switch {
    position: relative;
    height: toRem(20);
    width: toRem(40);
    border: toRem(1) solid $dark-shade-25;
    border-radius: toRem(10);
    margin-right: toRem(10);

    &::after {
      position: absolute;
      content: '';
      height: toRem(12);
      width: toRem(12);
      background-color: $dark-shade-25;
      border-radius: 50%;
      margin: auto 0;
      left: 9%;
      top: 0;
      bottom: 0;
      transition: transform 0.4s;
    }
  }
}